"use client";

import { useEffect, useState } from "react";
import { motion } from "framer-motion";
import Link from "next/link";
import { useRouter } from "next/navigation";
import { useAuthStore } from "../store/authStore";
import { problemsApi } from "../lib/api";
import { Problem } from "../lib/types";
import { Search, Filter, ArrowRight, BookOpen } from "lucide-react";

const DIFF_LABELS = ["", "Easy", "Easy-Med", "Medium", "Hard", "Expert"];
const DIFF_COLORS = ["", "text-green-600", "text-lime-600", "text-yellow-600", "text-red-600", "text-purple-600"];
const DIFF_BG = ["", "bg-green-50 border-green-200", "bg-lime-50 border-lime-200", "bg-yellow-50 border-yellow-200", "bg-red-50 border-red-200", "bg-purple-50 border-purple-200"];

export default function ProblemsPage() {
    const router = useRouter();
    const { isAuthenticated } = useAuthStore();
    const [problems, setProblems] = useState<Problem[]>([]);
    const [filtered, setFiltered] = useState<Problem[]>([]);
    const [loading, setLoading] = useState(true);
    const [search, setSearch] = useState("");
    const [diffFilter, setDiffFilter] = useState<number | null>(null);

    useEffect(() => {
        if (!isAuthenticated) { router.push("/login"); return; }
        problemsApi.list().then(({ data }) => {
            setProblems(data || []);
            setFiltered(data || []);
        }).catch(() => { }).finally(() => setLoading(false));
    }, [isAuthenticated, router]);

    useEffect(() => {
        let result = problems;
        if (search) result = result.filter((p) => p.title.toLowerCase().includes(search.toLowerCase()));
        if (diffFilter) result = result.filter((p) => p.difficulty === diffFilter);
        setFiltered(result);
    }, [search, diffFilter, problems]);

    const getDotColor = (d: number) => {
        const c = ["", "bg-green-400", "bg-lime-400", "bg-yellow-400", "bg-red-400", "bg-purple-400"];
        return c[d] || "bg-gray-400";
    };

    return (
        <div className="min-h-screen bg-[#faf9f6] pt-20">
            <div className="max-w-6xl mx-auto px-6 py-10">

                {/* Header */}
                <motion.div
                    className="mb-10"
                    initial={{ opacity: 0, y: 20 }}
                    animate={{ opacity: 1, y: 0 }}
                >
                    <div className="flex items-center gap-2 mb-3">
                        <BookOpen className="w-5 h-5 text-[#f97316]" />
                        <span className="text-xs font-bold uppercase tracking-widest text-[#f97316]">Problem Bank</span>
                    </div>
                    <h1 className="font-serif font-black text-4xl md:text-5xl text-[#0d0d0d] mb-3">
                        Browse Problems
                    </h1>
                    <p className="text-[#6b7280] text-lg">
                        50+ DSA problems across 30 concepts. Each one trains your thinking — not your memory.
                    </p>
                </motion.div>

                {/* Filters */}
                <motion.div
                    className="flex flex-col sm:flex-row gap-3 mb-8"
                    initial={{ opacity: 0, y: 10 }}
                    animate={{ opacity: 1, y: 0 }}
                    transition={{ delay: 0.1 }}
                >
                    <div className="relative flex-1">
                        <Search className="w-4 h-4 text-[#9ca3af] absolute left-3 top-1/2 -translate-y-1/2" />
                        <input
                            type="text"
                            placeholder="Search problems…"
                            value={search}
                            onChange={(e) => setSearch(e.target.value)}
                            className="w-full pl-10 pr-4 py-2.5 border border-[#e5e7eb] rounded-sm text-sm bg-white outline-none focus:border-[#f97316] transition-colors"
                        />
                    </div>
                    <div className="flex gap-2 flex-wrap">
                        <button
                            onClick={() => setDiffFilter(null)}
                            className={`px-3 py-2 text-xs font-semibold rounded-sm border transition-all ${!diffFilter ? "bg-[#0d0d0d] text-white border-[#0d0d0d]" : "bg-white text-[#6b7280] border-[#e5e7eb] hover:border-[#9ca3af]"}`}
                        >All</button>
                        {[1, 2, 3, 4, 5].map((d) => (
                            <button
                                key={d}
                                onClick={() => setDiffFilter(d === diffFilter ? null : d)}
                                className={`px-3 py-2 text-xs font-semibold rounded-sm border transition-all ${diffFilter === d ? `${DIFF_BG[d]} ${DIFF_COLORS[d]} border-current` : "bg-white text-[#6b7280] border-[#e5e7eb] hover:border-[#9ca3af]"}`}
                            >
                                {DIFF_LABELS[d]}
                            </button>
                        ))}
                    </div>
                </motion.div>

                {/* Problem count */}
                <p className="text-xs text-[#9ca3af] mb-4 uppercase tracking-widest">
                    {filtered.length} Problem{filtered.length !== 1 ? "s" : ""}
                </p>

                {/* Grid */}
                {loading ? (
                    <div className="flex justify-center py-20">
                        <div className="w-8 h-8 border-2 border-[#f97316] border-t-transparent rounded-full animate-spin" />
                    </div>
                ) : (
                    <div className="grid sm:grid-cols-2 lg:grid-cols-3 gap-4">
                        {filtered.map((p, i) => (
                            <motion.div
                                key={p.id}
                                initial={{ opacity: 0, y: 20 }}
                                animate={{ opacity: 1, y: 0 }}
                                transition={{ duration: 0.4, delay: i * 0.04 }}
                            >
                                <Link
                                    href={`/problems/${p.id}`}
                                    className="block bg-white border border-[#e5e7eb] rounded-2xl p-5 card-hover no-underline h-full group"
                                >
                                    <div className="flex items-start justify-between mb-3">
                                        <div className={`flex items-center gap-1.5 text-xs font-semibold px-2 py-1 rounded-full border ${DIFF_BG[p.difficulty]} ${DIFF_COLORS[p.difficulty]}`}>
                                            <span className={`w-1.5 h-1.5 rounded-full ${getDotColor(p.difficulty)}`} />
                                            {DIFF_LABELS[p.difficulty]}
                                        </div>
                                        <ArrowRight className="w-4 h-4 text-[#d1d5db] group-hover:text-[#f97316] transition-colors mt-0.5" />
                                    </div>
                                    <h3 className="font-semibold text-[#0d0d0d] text-sm mb-2 leading-snug">
                                        {p.title}
                                    </h3>
                                    <p className="text-xs text-[#9ca3af] line-clamp-2 leading-relaxed mb-3">
                                        {p.description}
                                    </p>
                                    <div className="flex flex-wrap gap-1">
                                        {p.concept_ids?.slice(0, 3).map((c) => (
                                            <span key={c} className="text-[10px] bg-[#f3f4f6] text-[#6b7280] px-2 py-0.5 rounded-full">
                                                {c}
                                            </span>
                                        ))}
                                    </div>
                                </Link>
                            </motion.div>
                        ))}
                        {filtered.length === 0 && (
                            <div className="col-span-3 text-center py-20 text-[#9ca3af]">
                                No problems match your filters.
                            </div>
                        )}
                    </div>
                )}
            </div>
        </div>
    );
}
